<template>
 <div>
    <page-layout>
      <div class="mdl-tabs mdl-js-tabs mdl-js-ripple-effect">
        <div class="mdl-tabs__tab-bar mdl-cell--bottom">
          <a href="#list" class="mdl-tabs__tab is-active">Materials</a>
          <a href="#create" class="mdl-tabs__tab">Add</a>
          <a href="#purchase" class="mdl-tabs__tab">Orders</a>
        </div>
        <div class="mdl-tabs__panel is-active" id="list">
          <raw-material-list-view></raw-material-list-view>
        </div>
        <div class="mdl-tabs__panel" id="create">
          <raw-material-create-view></raw-material-create-view>
        </div>
        <div class="mdl-tabs__panel" id="purchase">
          <raw-material-orders-view></raw-material-orders-view>
        </div>
      </div>
      <!-- <router-view></router-view> -->
    </page-layout>
 </div>
</template>
<script>
  import { PageLayout } from 'LAYOUTS_COMPONENTS'
  import { RawMaterialListView, RawMaterialCreateView, RawMaterialOrdersView } from '.'
  export default {
    name: 'vendors-view',
    components: {
      PageLayout,
      RawMaterialListView,
      RawMaterialCreateView,
      RawMaterialOrdersView
    }
  }
</script>



